import { capitalize, generateHash, generateToken, sanitize } from '../../../helpers/utilities';
import validateUseSignup from '../validators/signupValidation';

const createUser = ({
  email,
  password,
  first_name,
  middle_name = null,
  last_name,
  country_of_residence,
  username,
}) => {
  validateUseSignup(email, password, username, first_name, last_name, country_of_residence);
  const sanitizedPassword = sanitize(password);
  const hashedPassword = generateHash(sanitizedPassword);
  return Object.freeze({
    id: generateToken({ lengthOfToken: 22 }),
    type: 'individual',
    role: 'basic',
    status: 'active',
    first_name: capitalize(first_name.trim()),
    middle_name: capitalize(middle_name) ?? null,
    last_name: capitalize(last_name.trim()),
    email: email.trim().toLowerCase(),
    password: hashedPassword,
    username: `@${username}`,
    country_of_residence,
    is_email_verified: false,
    is_phone_verified: false,
    is_id_verified: false,
    created_at: new Date().toISOString(),
    updated_at: new Date().toISOString(),
    consents: JSON.stringify({
      privacy_policy: {
        accepted: true,
        accepted_at: new Date().toISOString(),
      },
      terms_of_service: {
        accepted: true,
        accepted_at: new Date().toISOString(),
      },
    }),
  });
};

const UserFactory = {
  createUser,
};

export default UserFactory;
